html, body {
  display: flex;
  width: 100%;
  height: 100%;
}

body {
  flex: 1;
  background: linear-gradient(180deg, hsl(235, 16%, 14%) 0%, hsl(236, 21%, 26%) 100%);
}

div {
  display: flex;
}

body h1 {
  flex: 1;
  justify-content: center;
}

.countdown {
  flex: 1;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.day, .hour, .minute, .second {
  width: 200px;
  height: 205px;
  margin: 25px;
  background-color: hsl(234, 17%, 12%);
  flex-direction: column;
  padding-bottom: 5px;
  border-radius: 20px;
  overflow: hidden;
}

.upper {
  flex: 1;
  border-radius: 20px 20px 0 0;
  align-items: center;
  background: linear-gradient(180deg,hsl(235, 16%, 14%) 0, hsl(235, 16%, 14%) 50%, hsl(236, 21%, 26%) 50%, hsl(236, 21%, 26%) 100%);

  &:before {
    content: "";
    align-self: center;
    background-color: black;
    position: relative;
    right: 10px;
    height: 20px;
    width: 20px;
    border-radius: 10px;
    z-index: 1;
  }

  &:after {
    content: "";
    align-self: center;
    background-color: black;
    position: relative;
    height: 20px;
    width: 20px;
    left: 10%;
    border-radius: 10px;
    z-index: 1;
  }
}

.number {
  flex: 1;
  font-size: 60px;
  text-align: center;
}

p {
  color: white;
  @font-face {
    font-family: Red Hat Text;
    src: url('https://fonts.google.com/specimen/Red+Hat+Text');
  }
  //font-size: 14px;
}
